<template>
  <div class="collection" style="width:100%;padding:10px">
    <div class="rg-top">
      <div class="rg-top-top">
        <span class="content-top-text-blue"></span>
        <span id="left_month">{{company[0].shenbao_month}}</span>
        <span style="margin-left: 2px">劳务报酬所得</span>
        <span class="f-14 ml-50" style="font-weight: normal">申报总人数：</span>
        <span id="top_sum">{{ payNormalTable.length }}</span>
        <span class="f-14 mr-20" style="font-weight: normal">人</span>
        <span class="f-14" style="font-weight: normal">收入总额：</span>
        <span id="top_add">{{ incomeAll }}</span>
        <span class="f-14 mr-20" style="font-weight: normal">人</span>
        <span class="f-14" style="font-weight: normal">免税收入合计：</span>
        <span id="top_del">{{ mianTaxAll }}</span>
        <span class="f-14" style="font-weight: normal">人</span>
      </div>
      <div class="rg-top-bottom">
        <div>
          <el-button type="primary" size="small" @click="goBack">返回</el-button>
          <el-button type="primary" size="small" @click="addFormClick">添加</el-button>
          <el-dropdown>
            <el-button type="primary" size="small" :split-button="true">
              导入
              <i class="el-icon-arrow-down el-icon--right"></i>
            </el-button>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item @click.native=" nullClick">导入文件</el-dropdown-item>
              <el-dropdown-item @click.native=" nullClick">模板下载</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
        <div>
          <el-dropdown>
            <el-button type="text" size="small" class="right-btn">
              导出
              <i class="el-icon-arrow-down el-icon--right"></i>
            </el-button>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item @click.native=" nullClick">导出当页</el-dropdown-item>
              <el-dropdown-item @click.native=" nullClick">导出全部</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
          <el-button type="text" size="small" class="right-btn" @click="changeSelectClick">展开查询条件</el-button>
          <el-dropdown>
            <el-button type="text" size="small" class="right-btn">
              更多操作
              <i class="el-icon-arrow-down el-icon--right"></i>
            </el-button>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item @click.native="updataClick(formId)">修改</el-dropdown-item>
              <el-dropdown-item @click.native="deleteClick(formId)">删除</el-dropdown-item>
              <el-dropdown-item @click.native=" nullClick">批量修改</el-dropdown-item>
              <el-dropdown-item @click.native=" nullClick">自定义显示列</el-dropdown-item>
              <el-dropdown-item @click.native=" nullClick">隐藏非正常人员</el-dropdown-item>
              <el-dropdown-item @click.native=" nullClick">异常身份证处理</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </div>
    </div>
    <topSelect v-if="isSelect" :formId="formId" @selectClick="selectClick"></topSelect>
    <el-table
      :data="payNormalTable"
      border
      fit
      size="mini"
      :ref="formId+'Table'"
      @selection-change="selsChange"
      style="margin-top: 5px;"
    >
      <el-table-column type="selection"></el-table-column>
      <el-table-column prop="employmentNumber" label="工号"></el-table-column>
      <el-table-column prop="personName" label="姓名"></el-table-column>
      <el-table-column prop="cardType" label="证照类型"></el-table-column>
      <el-table-column prop="cardNumber" label="证照号码" show-overflow-tooltip></el-table-column>
      <el-table-column prop="incomeProject" label="所得项目" show-overflow-tooltip></el-table-column>
      <el-table-column label="所得期间起" width="100px">
        <template>{{company[0].time_start}}</template>
      </el-table-column>
      <el-table-column label="所得期间止" width="100px">
        <template>{{company[0].time_end}}</template>
      </el-table-column>
      <el-table-column prop="income" label="本期收入"></el-table-column>
      <el-table-column prop="pay" label="费用"></el-table-column>
      <el-table-column prop="incomeNoTax" label="本期免税收入" width="120px"></el-table-column>
      <el-table-column prop="otherForm.shangyebaoxian" label="商业健康保险" width="120px"></el-table-column>
      <el-table-column prop="otherForm.shuiyanbaoxian" label="税延养老保险" width="120px"></el-table-column>
      <el-table-column prop="otherForm.else" label="其他"></el-table-column>
      <el-table-column prop="otherAll.zhunyukouchu" label="准予扣除的捐赠额" width="150px"></el-table-column>
      <el-table-column prop="otherAll.jianmianshuie" label="减免税额" width="120px"></el-table-column>
      <el-table-column prop="otherAll.yikoujiaoshuie" label="已扣缴税额" width="140px"></el-table-column>
      <el-table-column prop="otherAll.remark" label="备注" width="120px"></el-table-column>
    </el-table>

    <!-- 新增和修改表的弹窗 -->
    <el-dialog
      class="in-table"
      title="劳务报酬所得"
      :visible.sync="centerDialogVisible"
      :close-on-click-modal="close_modal"
      width="1000px"
      top="10vh"
    >
      <div class="in-table-content">
        <!-- 表单开始的地方 -->
        <el-form
          :status-icon="true"
          :model="laborForm"
          :rules="rules"
          ref="laborForm"
          label-width="120px"
          class="demo-ruleForm"
          label-position="right"
        >
          <!-- 弹窗所有的表格信息 限定为600px高度-->
          <div style="height:600px;padding:0 30px 0 0px;overflow-y:scroll">
            <!-- 基本信息 -->
            <div class="main-info">
              <div style="height:40px;line-height:40px;position:relative;left:5px">
                <span class="content-top-text-blue"></span>
                <span style="margin-left: 2px">基础信息</span>
              </div>
              <div style="display:flex">
                <el-form-item label="工号" style="width:33%">
                  <el-select v-model="laborForm.personInfoId" placeholder="-工号-">
                    <el-option
                      v-for="(item, i) in personInfo"
                      v-if="item.employmentType === '其他'"
                      :key="i"
                      :label="item.employmentNumber?item.employmentNumber:'暂未填写'"
                      :value="item.infoId"
                    ></el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="证照类型" prop="cardType" style="width:50%">
                  <el-input value="身份证" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="所得时间起" prop="cardNumber" style="width:50%">
                  <el-input :value="company[0].time_start" readonly></el-input>
                </el-form-item>
              </div>
              <div style="display:flex">

                <el-form-item label="姓名" style="width:33%" prop="personInfoId">
                  <el-select v-model="laborForm.personInfoId" placeholder="-请选择姓名-">
                    <el-option
                      v-for="(item, i) in personInfo"
                      v-if="item.employmentType === '其他'"
                      :key="i"
                      :label="item.personName"
                      :value="item.infoId"
                    ></el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="证照号码" prop="cardType" style="width:50%">
                  <el-select v-model="laborForm.personInfoId" placeholder="请选择证件类型">
                    <el-option
                      v-for="(item, i) in personInfo"
                      v-if="item.employmentType === '其他'"
                      :key="i"
                      :label="item.cardNumber"
                      :value="item.infoId"
                    ></el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="所得时间止" prop="cardNumber" style="width:50%">
                  <el-input :value="company[0].time_end" readonly></el-input>
                </el-form-item>
              </div>
              <div style="display:flex">
                <el-form-item label="所得项目" style="width:33%">
                  <el-select v-model="laborForm.incomeProject" placeholder="-请选择所得项目-">
                    <el-option value="一般劳动项目所得"></el-option>
                    <el-option value="其他劳动项目所得"></el-option>
                  </el-select>
                </el-form-item>
              </div>
            </div>
            <div class="main-info">
              <div style="height:40px;line-height:40px;position:relative;left:5px">
                <span class="content-top-text-blue"></span>
                <span style="margin-left: 2px">本期收入及免税收入</span>
              </div>
              <div style="display:flex">
                <el-form-item label="本期收入" prop="cardType" style="width:33%">
                  <el-input type="number" v-model.number="laborForm.income" @input="feiyong"></el-input>
                </el-form-item>
                <el-form-item label="费用" prop="cardNumber" style="width:33%">
                  <el-input type="number" v-model.number="laborForm.pay" readonly></el-input>
                </el-form-item>
                <el-form-item label="本期免税收入" prop="cardNumber" style="width:33%">
                  <el-input type="number" v-model.number="laborForm.incomeNoTax"></el-input>
                </el-form-item>
              </div>
            </div>

            <div class="main-info">
              <div style="height:40px;line-height:40px;position:relative;left:5px">
                <span class="content-top-text-blue"></span>
                <span style="margin-left: 2px">本期其他扣除</span>
              </div>
              <div style="display:flex">
                <el-form-item label="商业健康保险" prop="cardNumber" style="width:33%">
                  <el-input type="number" v-model.number="laborForm.otherForm.shangyebaoxian"
                            @input="benqikouchuxioaji"></el-input>
                </el-form-item>
                <el-form-item label="税延养老保险" prop="cardNumber" style="width:33%">
                  <el-input type="number" v-model.number="laborForm.otherForm.shuiyanbaoxian"
                            @input="benqikouchuxioaji"></el-input>
                </el-form-item>
                <el-form-item label="其他" prop="cardType" style="width:33%">
                  <el-input type="number" v-model.number="laborForm.otherForm.else"
                            @input="benqikouchuxioaji"></el-input>
                </el-form-item>
              </div>
              <div style="display:flex">
                <el-form-item label="小计" prop="cardNumber" style="width:33%">
                  <el-input type="number" v-model.number="laborForm.otherForm.benqikouchuxioaji"></el-input>
                </el-form-item>
              </div>
            </div>

            <div class="main-info">
              <div style="height:40px;line-height:40px;position:relative;left:5px">
                <span class="content-top-text-blue"></span>
                <span style="margin-left: 2px">本期其他</span>
              </div>
              <div style="display:flex">
                <el-form-item label="准予扣除的捐赠额" prop="cardType" style="width:33%">
                  <el-input type="number" v-model.number="laborForm.otherAll.zhunyukouchu"></el-input>
                </el-form-item>
                <el-form-item label="减免税额" prop="cardNumber" style="width:33%">
                  <el-input type="number" v-model.number="laborForm.otherAll.jianmianshuie"></el-input>
                </el-form-item>
              </div>
              <div style="display:flex">
                <el-form-item label="已缴纳税额" prop="cardType" style="width:33%">
                  <el-input type="number" v-model.number="laborForm.otherAll.yikoujiaoshuie"></el-input>
                </el-form-item>
              </div>
              <div style="display:flex">
                <el-form-item label="备注" prop="cardType" style="width:100%">
                  <el-input v-model="laborForm.otherAll.remark"></el-input>
                </el-form-item>
              </div>
            </div>

            <!-- 表格填写信息结束 -->
          </div>
          <el-form-item class="btn-bottom">
            <el-button type="primary" size="small" @click="submitForm(formId)">保存</el-button>
            <el-button size="small" @click="resetForm(formId)">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import formHelper from '@/until/formHelper'
import dataHelper from '@/until/dataHelper'
import commonData from '@/until/commonData'
import topSelect from './components/top-select'

var tableName = 'laborForm'
export default {
  components: {
    topSelect
  },
  data () {
    return {
      ...commonData,
      formId: tableName,
      close_modal: false,
      incomeProject: '一般劳动项目所得',
      laborForm: {
        personInfoId: '',
        income: 0,
        pay: 0,
        incomeNoTax: 0,
        otherForm: {
          yearIncome: 0,
          shangyebaoxian: 0,
          shuiyanbaoxian: 0,
          else: 0,
          benqikouchuxioaji: 0
        },
        otherAll: {
          zhunyukouchu: 0,
          jianmianshuie: 0,
          jianchufeiyongbiaozhun: 0,
          yikoujiaoshuie: 0,
          remark: ''
        }
      },
      rules: {
        personInfoId: [
          { required: true, message: '请选择姓名' }
        ]
      },
      incomeAll: 0,
      mianTaxAll: 0
    }
  },
  computed: {
    ...mapGetters(['company']),
    personInfo: {
      get () {
        console.log(dataHelper.tableRelevance(this, 'personCollection', ['personName', 'cardNumber', 'cardType', 'infoId', 'employmentNumber'], { employmentType: '雇员' }))
        return dataHelper.tableRelevance(this, 'personCollection', ['personName', 'cardNumber', 'cardType', 'infoId', 'employmentNumber', 'employmentType'], { employmentType: '雇员' })
      }
    },
    payNormalTable: {
      get () {
        if (!this.isSelectNow) {
          let b = dataHelper.tableRelevanceArr(this, this.$store.state.form[tableName], this.$store.getters.getFormContent('personCollection'), ['personName', 'cardNumber', 'cardType', 'employmentNumber'], 'personInfoId', 'infoId')
          return b
        } else {
          return this.selectArr
        }
      }
    }
  },
  watch: {
    centerDialogVisible (val) {
      if (!val) {
        this.isUpdata = false
        this[tableName] = this.$options.data()[tableName]
      }
    }
  },
  created () {
    this.$emit('router', this.$router.history.current.name)
    this.calculate()
  },
  mounted () {

  },
  methods: {
    ...formHelper,
    calculate () {
      if (this.payNormalTable.length > 0) {
        this.payNormalTable.forEach(v => {
          this.incomeAll += v.income
          this.mianTaxAll += v.incomeNoTax
        })
      }
    },
    submitForm (formName) {
      var _this = this
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let submitData = {} // 用于提交的主键，格式参考store
          if (!_this[formName].infoId) {
            _this[formName].infoId = _this.$store.getters.getFormInfoId(formName) + 1
          }
          let monthNumber = _this.$store.state.service.monthNumber
          // 累计收入
          _this[formName].incomeAll = _this[formName].income
          // 累计免税收入
          _this[formName].incomeNoTaxAll = _this[formName].incomeNoTax
          // 累计减除费用
          _this[formName].otherAll.jianchufeiyongbiaozhunAll = _this[formName].pay
          // 累计扣除及减除项目合计 = 其他扣除小计
          _this[formName].jianchuAll = _this[formName].pay
          // 累计准予扣除的捐赠额
          _this[formName].otherAll.zhunyukouchuAll = _this[formName].otherAll.zhunyukouchu
          // 累计应纳税所得额 = 含税奖金额 - 扣除项目合计
          // _this[formName].leijiNashuiAll = ((parseFloat(_this[formName].income) - _this[formName].jianchuAll - parseFloat(_this[formName].otherAll.jianchufeiyongbiaozhun) - parseFloat(_this[formName].incomeNoTax) - parseFloat(_this[formName].otherAll.zhunyukouchu))) * monthNumber.toFixed(2)
          _this[formName].leijiNashuiAll = parseFloat(_this[formName].incomeAll) - parseFloat(_this[formName].incomeNoTaxAll) - parseFloat(_this[formName].otherAll.jianchufeiyongbiaozhunAll) - parseFloat(_this[formName].jianchuAll) - parseFloat(_this[formName].otherAll.jianchufeiyongbiaozhunAll)
          if (_this[formName].leijiNashuiAll <= 0) {
            _this[formName].leijiNashuiAll = 0
          }
          // 应纳税额
          _this[formName].taxAll = dataHelper.laowuShuilvPercent(_this[formName].leijiNashuiAll)
          // 累计减免税额
          _this[formName].otherAll.jianmianshuieAll = _this[formName].otherAll.jianmianshuie
          // 累计应扣缴税额 = 应纳税所得额 - 减免税额
          _this[formName].yingkoujiaoshuie = _this[formName].taxAll.taxPay - parseFloat(_this[formName].otherAll.jianmianshuie)
          // 应补(退)税额 = 应纳税所得额 - 减免税额
          _this[formName].tuibu = _this[formName].yingkoujiaoshuie - _this[formName].otherAll.yikoujiaoshuie
          submitData.formId = formName
          submitData.content = _this[formName]
          if (_this.isUpdata) {
            _this.$store.commit('updataFormInfo', submitData)
            _this.isUpdata = false
          } else {
            _this.$store.commit('addFormInfo', submitData)
          }

          _this.centerDialogVisible = false
          this[formName] = this.$options.data()[formName]
          // alert('提交成功!')
          _this.$message.success('提交成功!')
          console.log(this.$store.getters.getFormContent(tableName))
          this.calculate()
        } else {
          return false
        }
      })
    },
    benqikouchuxioaji () {
      let inputDom = this.laborForm.otherForm
      inputDom.benqikouchuxioaji = (parseFloat(inputDom.shangyebaoxian) + parseFloat(inputDom.shuiyanbaoxian) + parseFloat(inputDom.else)).toFixed(2)
    },
    // feiyong () {
    //   let inputDom = this.laborForm
    //   let income = parseFloat(inputDom.income)
    //   switch (true) {
    //     case income <= 4000:
    //       inputDom.pay = 800
    //       break
    //     case income > 4000 && income <= 20000:
    //       inputDom.pay = income - income * (1 - 0.2)
    //       inputDom.pay = inputDom.pay.toFixed(2)
    //       break
    //     case income > 20000 && income <= 50000:
    //       inputDom.pay = income - income * (1 - 0.3) - 2000
    //       inputDom.pay = inputDom.pay.toFixed(2)
    //       break
    //     case income > 50000:
    //       inputDom.pay = income - income * (1 - 0.4) - 7000
    //       inputDom.pay = inputDom.pay.toFixed(2)
    //       break
    //   }
    // }
    feiyong () {
      let inputDom = this.laborForm
      let income = parseFloat(inputDom.income)
      switch (true) {
        case income <= 4000:
          inputDom.pay = 800
          break
        case income > 4000:
          inputDom.pay = income - income * (1 - 0.2)
          inputDom.pay = inputDom.pay.toFixed(2)
          break
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  li {
    display: block;
    margin: 0;
  }

  a {
    color: #42b983;
  }

  img {
    vertical-align: middle;
  }

  table {
    width: 10% !important;
  }

  .collection {
    font-size: 12px;
    text-align: start;
    margin-top: 10px;
  }

  .hot-notice {
    border: solid 1px #d2e7fb;
    background-color: #eaf4ff;
    box-sizing: border-box;
    margin: 10px 35px 0 10px;
    padding: 8px;
    line-height: 20px;
    text-align: left;
  }

  .rg-top {
    border: solid 1px #e0e0e0;
    padding: 0 10px;
    font-size: 16px;
    font-weight: bold;
    color: #000000;
  }

  .rg-top-top {
    height: 40px;
    overflow: hidden;
    padding: 0 10px;
    line-height: 40px;
    box-sizing: border-box;
  }

  .rg-top-bottom {
    height: 40px;
    padding: 0 10px;
    line-height: 30px;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
  }

  .right-btn {
    padding: 8px 10px 8px 10px;
    border: 1px #5a98de solid;
    border-radius: 5px;
    font-size: 14px;
  }

  .right-btn:hover {
    background-color: rgb(64, 158, 255);
    color: white;
  }

  .con_btn1 {
    float: left;
    width: 112px;
    height: 33px;
    line-height: 33px;
    border: solid #4586f5;
    border-width: 0px 0px 2px 0px;
    font-size: 15px;
    color: #4586f5;
    text-align: center;
    background-color: #ffffff;
    cursor: pointer;
  }

  .con_btn2 {
    float: left;
    width: 112px;
    height: 33px;
    line-height: 33px;
    border: solid transparent;
    border-width: 0 0 2px 0;
    font-size: 15px;
    color: #000000;
    text-align: center;
    background-color: #ffffff;
    cursor: pointer;
  }

  .in-table .notice {
    display: block;
    width: 100%;
    height: 20px;
    /* display: block; */
    background-color: rgb(212, 244, 252);
  }

  .in-table-content {
    padding: 0 30px;
  }

  .mianshui-notice {
    font-size: 12px;
    padding: 1px 10px 20px 10px;
  }
</style>
<style>
  .el-form-item {
    margin-bottom: 10px;
  }

  .el-form-item__label {
    width: 120px;
  }

  .el-select {
    width: 100%;
  }

  .el-form-item__label {
    font-size: 12px;
  }
</style>



